import React from 'react'
import img from '../../data/images'
import SliderSlick from "react-slick";
import icon from '../../data/icons';
export default function SliderComment() {
const commentItem = [
  { img: img.home_sec_8_1, title: <>  <br/> <span></span></>, text: '     !       .     -  ,  !       .' },
  { img: img.home_sec_8_2, title: <>  <br/> <span></span></>, text: '      !        ,     !  ,      !' },
  { img: img.home_sec_8_3, title: <> <br/> <span></span></>, text: '   ,     ,      .      ,          !        .' },
  { img: img.home_sec_8_1, title: <>  <br/> <span></span></>, text: '     !       .     -  ,  !       .' },
  { img: img.home_sec_8_2, title: <>  <br/> <span></span></>, text: '      !        ,     !  ,      !' },
  { img: img.home_sec_8_3, title: <> <br/> <span></span></>, text: '   ,     ,      .      ,          !        .' },
  ]
  const settings = {
    dots: true,
    infinite: true,
    speed: 900,
    autoplaySpeed: 10000, //   
    slidesToShow: 3,
    slidesToScroll: 2,
    arrows: false,
    autoplay: false,
   pauseOnHover: true,
   responsive: [ //   768
   {
     breakpoint: 850,
     settings: {
       slidesToShow: 1,
       slidesToScroll: 1,
     }
   },
  ]
  };
  return (
    <section className="home-8">
    <div className="container h8-container">
    <h2 className="h8-title">    </h2>
    <div className="h8-comment__wrapper">
    <div className="h8-comment__row">
    <SliderSlick {...settings} >
      {
        commentItem.map((item, i)=> { 
         return ( 
       <div key={i}  className="h8-comment__column">
          <div className="h8-comment__item">
            <div className="h8-commentItem__row">
              <div className="h8-commentItem__image"><img src={item.img} alt=""/></div>
              <div className="h8-commentItem__title">{item.title}</div>
              <div className="h8-commentItem__Quote"><img src={icon.home_quote} alt="icon"/></div>
            </div>
            <h4 className="h8-commentItem__text">{item.text}</h4>
          </div>
        </div>
          )})  
      }
     </SliderSlick>
    </div>
    </div>
    </div>
    </section>
  )
}
